.coupon-details {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #FAFAFA;
  }
  
  .coupon-details h2 {
    font-size: 24px;
    margin-bottom: 10px;
    text-shadow: 1px 1px 2px grey;
    text-align: center;
  }
  
  .coupon-details p {
    font-size: 16px;
    margin-bottom: 5px;
    text-shadow: 1px 1px 2px grey;
  }
  
  .coupon-details img {
    max-width: 100%;
    height: auto;
    margin-top: 10px;
  }