.coupon-item {
    background-color: #FAFAFA;
    width: 15vw;
    height: auto;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 3px 3px 2px #444;
    padding: 5px;
    overflow: visible;
    border-radius: 10px;
}

.coupon-item-actions{
    margin: 3px;
    padding: 3px;
    display: flex;
    justify-content: space-around;
}