body{
    margin: 5px;
    padding: 5px;
    background-color: #c6e2ff;
}

.raise:hover,
.raise:focus {
  box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
  transform: translateY(-0.25em);
}

.divider{
  width:5px;
  height:auto;
  display:inline-block;
}

.App h2{
    text-align: center;
    margin-bottom: 10px;
    text-shadow: 1px 1px 2px grey;
}

.App h6{
    text-align: center;
}

.title {
    font-size: 30px;
    text-shadow: 1px 1px 2px grey;
  }